.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

html {
  overflow-y: scroll;
}
a {
  text-decoration: none !important;
  color: #519cdd;
}
h1 {
  text-transform: uppercase;
}
.row {
  display: flex;
  flex-direction: row;
}
.row .col-md-1 {
  width: 8.33%;
}
.row .col-md-2 {
  width: 16.67%;
}
.row .col-md-3 {
  width: 25%;
}
.row .col-md-4 {
  width: 33.33%;
}
.row .col-md-5 {
  width: 41.67%;
}
.row .col-md-6 {
  width: 50%;
}
.row .col-md-7 {
  width: 58.33%;
}
.row .col-md-8 {
  width: 66.67%;
}
.row .col-md-9 {
  width: 75%;
}
.row .col-md-10 {
  width: 83.33%;
}
.row .col-md-11 {
  width: 91.67%;
}
.row .col-md-12 {
  width: 100%;
}
.btn {
  padding: 10px 12px;
}
.btn:hover {
  cursor: pointer;
}
.btn.btn-primary {
  background-color: #519cdd;
  color: #fff;
  border: 1px solid transparent;
}
.mar-0 {
  margin: 0;
}
.pad-0 {
  padding: 0 !important;
}
.cms-container {
  height: 100%;
  padding: 0;
  /* margin: 0; */
}
.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
.row {
  width: 100%;
  margin: 0;
}
.left-menu-col,
.right-menu-col {
  width: 40%;
}

.col-md-1,
.cms-col {
  height: 100%;
  padding: 0 8px;
  box-sizing: border-box;
}
.img-container {
  margin: 0 auto;
  width: 50px;
  height: 50px;
  display: block;
  overflow: hidden;
  border-radius: 50%;
}
.img-container > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.body-container {
  display: grid;
}

.btn .hire-me {
    margin: 10px auto;
    line-height: 1.5;
    border: 1px solid #519cdd;
    background-color: transparent;
    color: #428bca;
    display: flow-root;
    width: fit-content;
    min-width: 100px;
    padding: 6px 10px;
    text-align: center;
}
.btn .hire-me:hover {
  background: #77baf5;
  border: 1px solid #428bca;
  color: #fff;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 787px) {
  .container .row {
    margin-right: 0;
    margin-left: 0;
  }
}

.alert {
  position: relative;
  background: #eee;
  padding: 7px 7px 7px 12px;
  border: 1px solid #ddd;
  border-radius: 12px;
  display: flex;
  justify-content: space-between;
  gap: 16px;
  margin-bottom: 28px;
}
.alert.success {
  background-color: #1fd3452e;
  border-color: #0e7124d6;
}
.alert.success p {
  color: #48e269b6;
  font-weight: 14px;
}
.alert.success h3 {
  color: #48e26a;
  font-size: 16px;
}
.alert.error {
  background-color: #d31f3d18;
  border-color: #710e1bd6;
}
.alert.error p {
  color: #e24862b6;
  font-weight: 14px;
}
.alert.error h3 {
  color: #e24869;
  font-size: 16px;
}
.alert-close-btn {
  background: var(--onyx);
  border-radius: 8px;
  width: 24px;
  min-width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--white-2);
  font-size: 14px;
  opacity: 0.7;
  cursor: pointer;
}

.alert-close-btn:hover,
.alert-close-btn:focus { opacity: 1; }

.alert-close-btn ion-icon { --ionicon-stroke-width: 50px; }




